@-webkit-keyframes scroll-left {
    0% { -webkit-transform: translate(100%); transform: translate(100%); }
    100% { -webkit-transform: translate(-500%); transform: translate(-500%); }
}

@keyframes scroll-left {
    0% { -webkit-transform: translate(100%); transform: translate(100%); }
    100% { -webkit-transform: translate(-500%); transform: translate(-500%); }
}

